export enum AccountType {
  Savings = 'savings',
  Debit = 'debit',
  CreditCard = 'creditCard',
}

// –¢–∏–ø—ã —Ä–∞—Å—á—ë—Ç–Ω–æ–≥–æ –ø–µ—Ä–∏–æ–¥–∞
export type BillingCycleType =
  | 'fixed'       // —Ñ–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω—ã–π —Ü–∏–∫–ª N –¥–Ω–µ–π –æ—Ç –¥–∞—Ç—ã –≤—ã–ø–∏—Å–∫–∏
  | 'calendar'    // –∫–∞–ª–µ–Ω–¥–∞—Ä–Ω—ã–π –º–µ—Å—è—Ü
  | 'perPurchase' // –∏–Ω–¥–∏–≤–∏–¥—É–∞–ª—å–Ω—ã–π –ø–µ—Ä–∏–æ–¥ –æ—Ç –¥–∞—Ç—ã –ø–æ–∫—É–ø–∫–∏

// –î–µ—Ç–∞–ª–∏ –∫—Ä–µ–¥–∏—Ç–Ω–æ–π –∫–∞—Ä—Ç—ã / –∫—Ä–µ–¥–∏—Ç–Ω–æ–≥–æ —Å—á—ë—Ç–∞
export interface ICreditCardDetails {
  // –ª–∏–º–∏—Ç
  creditLimit: number;

  // –ª—å–≥–æ—Ç–Ω—ã–π –ø–µ—Ä–∏–æ–¥ (–±–µ—Å–ø—Ä–æ—Ü–µ–Ω—Ç–Ω—ã–π)
  gracePeriodDays: number;

  // —Ä–∞—Å—á—ë—Ç–Ω—ã–π –ø–µ—Ä–∏–æ–¥
  billingCycleType: BillingCycleType;
  // –µ—Å–ª–∏ fixed:
  billingCycleLengthDays?: number;
  // –µ—Å–ª–∏ calendar:
  billingCycleStartDayOfMonth?: number;
  // –µ—Å–ª–∏ perPurchase ‚Äî –Ω–µ –Ω—É–∂–Ω–æ –¥–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω—ã—Ö –ø–æ–ª–µ–π

  // –ø–ª–∞—Ç—ë–∂–Ω—ã–π –ø–µ—Ä–∏–æ–¥
  paymentPeriodDays: number;

  // –ø—Ä–æ—Ü–µ–Ω—Ç–Ω–∞—è —Å—Ç–∞–≤–∫–∞ (–≥–æ–¥–æ–≤—ã—Ö)
  interestRate: number;

  // –≥–æ–¥–æ–≤–∞—è –∫–æ–º–∏—Å—Å–∏—è
  annualFee?: number;

  // –∫–æ–º–∏—Å—Å–∏—è –∑–∞ —Å–Ω—è—Ç–∏–µ –Ω–∞–ª–∏—á–Ω—ã—Ö
  cashWithdrawalFeePercent?: number;
  cashWithdrawalFeeFixed?: number;
  cashWithdrawalLimitPerMonth?: number;

  // –∫—ç—à–±—ç–∫ (max% –∏ –∫–∞—Ç–µ–≥–æ—Ä–∏–∏)
  cashbackPercentMax?: number;
  cashbackCategories?: string[];
}

// –û–±—â–∏–π –∏–Ω—Ç–µ—Ä—Ñ–µ–π—Å –¥–ª—è —Å—É—â–Ω–æ—Å—Ç–∏ ¬´–°—á—ë—Ç¬ª
export interface IAccount {
  _id?: string;
  userId: string;         // —á–µ–π —ç—Ç–æ —Å—á—ë—Ç
  name: string;           // –ø—Ä–æ–∏–∑–≤–æ–ª—å–Ω–æ–µ –Ω–∞–∑–≤–∞–Ω–∏–µ
  type: AccountType;
  balance: number;
  currency: string;
  creditDetails?: ICreditCardDetails;  // —Ç–æ–ª—å–∫–æ –¥–ª—è type = 'credit'
  deletedAt?: Date;        // soft-delete
}
